<template>
  <div id="e3" style="max-width: 500px; margin: auto;" class="grey lighten-3">
    <v-toolbar color="purple" dark>
      <v-toolbar-side-icon></v-toolbar-side-icon>
      <v-toolbar-title>DC inside</v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>

    <v-card>
      <v-container fluid grid-list-lg>
        <v-layout row wrap class="scroll-y" style="height: 800px">
          <v-flex xs12 v-for="item in items" :key="item.number">
            <v-card color="white">
              <template v-if="item.images.length == 1">
                <v-img
                  :src="item.images[0]"
                  height="160px"
                  position="top"
                  :lazy-src="`https://picsum.photos/10/6?image=${1 * 5 + 10}`"
                >
                  <template v-slot:placeholder>
                    <v-layout fill-height align-center justify-center ma-0>
                      <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                    </v-layout>
                  </template>
                </v-img>
                <v-card-title primary-title>
                  <span class="font-weight-medium text-truncate headline">{{ item.title }}</span>
                  <div></div>
                  <span class="text-truncate">{{ item.content }}</span>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn flat class="white--text" color="orange" @click="hide(item.number)">숨기기</v-btn>
                  <v-btn flat class="white--text" :href="item.images[0]" color="purple">다운로드</v-btn>
                </v-card-actions>
              </template>
              <template v-else-if="item.images.length > 1">
                <v-container grid-list-sm fluid>
                  <v-layout row wrap>
                    <v-flex v-for="i in item.images" :key="i" xs4 d-flex>
                      <v-card flat tile class="d-flex">
                        <v-img
                          :src="i"
                          :lazy-src="`https://picsum.photos/10/6?image=${2 * 5 + 10}`"
                          aspect-ratio="1"
                          class="grey lighten-2"
                        >
                          <template v-slot:placeholder>
                            <v-layout fill-height align-center justify-center ma-0>
                              <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                            </v-layout>
                          </template>
                        </v-img>
                      </v-card>
                    </v-flex>
                  </v-layout>
                </v-container>
                <v-divider></v-divider>
                <v-card-title primary-title>
                  <div>
                    <span>{{ item.title }}</span>
                  </div>
                </v-card-title>
                <v-divider></v-divider>
              </template>
              <template v-else>
                <v-card-title primary-title>
                  <span class="font-weight-medium">{{ item.title }}</span>
                  <span class="text-truncate">{{ item.content }}</span>
                </v-card-title>
              </template>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      items: []
    };
  },

  mounted() {
    this.$sse("/api/sse", { format: "plain" }).then(sse => {
      sse.subscribe("", data => {
        if (this.items.length > 5) {
          this.items.pop();
        }
        let message = JSON.parse(data);
        this.items.unshift({
          title: message.title,
          images: message.images,
          number: message.number,
          content: message.content
        });
      });

      // eslint-disable-next-line
      sse.onError(e => {
        sse.close();
      });
    });
  },

  methods: {
    toggle(index) {
      const i = this.selected.indexOf(index);

      if (i > -1) {
        this.selected.splice(i, 1);
      } else {
        this.selected.push(index);
      }
    },
    hide(number) {
      let index = this.items.findIndex(i => {
        return i.number === number;
      });
      this.items.splice(index, 1);

      axios.get("/api/hide").then(response => {
        console.log(response);
      });
    }
  }
};
</script>